import uniList from '../../components/uni-list/uni-list.vue'
import uniItem from '../../components/uni-list-item/uni-list-item.vue'
export default {
	data() {
		return {
			waitTime:'',
			avatarUrl: '',
			name: '',
			isVip: '2',
			superiorId: '',
			openId: ''
		}
	},
	onShow() {
		uni.getSetting({
			success:res=>{
				if (res.authSetting['scope.userInfo']){
					this.getUserInfo()
				}
			}
		})
		uni.getStorage({
			key: 'openId',
			success: res => {
				this.openId = res.data
			}
		})
	},
	onLoad() {
		this.getOpenId()
	},
	methods: {
		getOpenId() {
			uni.login({
				success: res => {
					if (res.code) {
						uni.getUserInfo({
							success: res => {
								// console.log('存在code')
							}
						})
						let code = res.code
						uni.request({
							url: 'https://www.finetwm.com/xmRepair/userInfo/login',
							data: {
								code: code
							},
							method: 'GET',
							success: res => {
								// this.openId = res.data.openid
								console.log(res.data.data.openid)
								this.openId = res.data.data.openid
								uni.setStorage({
									key: 'openId',
									data: this.openId,
									success:res=>{
										console.log('success storage')
									}
								})
								// obj.expires_in = Date.now() + res.data.expires_in
							}
						});
					} else {
						console.log('获取用户登录态失败！' + res.errMsg);
					}
				}
			})
		},
		getUserInfo(){
			uni.getUserInfo({
				success:res=>{
					this.avatarUrl = res.userInfo.avatarUrl
					this.name = res.userInfo.nickName
				}
			})
		},
		
		onBeVip() {
			uni.navigateTo({
				url: '../bevip/bevip',
				success: res => {
					console.log('success')
				}
			});
		},
		onOrderList() {
			uni.navigateTo({
				url: '../orderlist/orderlist'
			});
		},
		onMyWallet() {
			uni.navigateTo({
				url: '../mywallet/mywallet'
			});
		},
		onAddressList() {
			uni.navigateTo({
				url: '../addresslist/addresslist'
			});
		},
		onBusinessIn() {
			uni.navigateTo({
				url: 'business-in'
			})
		},
		onFriends() {
			uni.navigateTo({
				url: 'my-friends'
			})
		},
		onCards() {
			uni.navigateTo({
				url: 'cards'
			})
		},
		onlineKf() {
			uni.navigateTo({
				url: 'online-kf'
			})
		}
	},
	components: {
		uniList,
		uniItem
	},
}
